@import "https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400&family=Montserrat:wght@200;400;600;800&display=swap";
html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
  overflow-x: hidden;
}

div#main {
  width: 100vw;
  flex-direction: column;
  display: flex;
}

section {
  width: 100vw;
  position: relative;
}

section.min-height {
  min-height: 100vh;
}

section.padding {
  padding: 5rem 0;
}

section img.background {
  height: 100%;
  object-fit: cover;
  pointer-events: none;
  width: 100vmax;
  z-index: -1;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%);
}

h1 {
  margin: 0;
  font-family: Cormorant Garamond, serif;
  font-size: 7rem;
  font-weight: 400;
  line-height: .8;
}

h2 {
  margin: 0;
  padding-top: 1rem;
  font-size: 2rem;
  font-weight: 400;
}

h3 {
  margin: 0;
  font-size: 1.25rem;
  font-weight: 400;
}

p {
  margin-bottom: 0;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.5;
}

p.large {
  font-size: 24px;
}

p.small {
  font-size: 15px;
}

hr {
  margin: 2.5rem;
}

li {
  line-height: 1.7;
}

li::marker {
  color: #4e567e;
}

a {
  color: #000;
  text-decoration: none;
}

a:hover {
  color: #4e567e;
}

.container {
  grid-gap: 20px;
  grid-template-columns: 330px 300px;
  margin: 20px auto;
  display: grid;
}

.container .box {
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 10px;
  flex-direction: column;
  justify-content: flex-start;
  padding: 24px 16px;
  display: flex;
}

img.socialIcon {
  height: 30px;
  width: 30px;
}

.about {
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background: url("crowd1.21e15697.jpg") center / cover no-repeat fixed;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.about__content {
  width: 50%;
  text-align: center;
  background-color: #fff;
  margin: 3rem auto;
  padding: 4rem;
}

.about__description {
  font-size: 1.25rem;
}

.about__skills {
  text-align: left;
  columns: 2;
  gap: 3rem;
  margin: 2rem 3rem;
  font-size: 1.25rem;
}

.about__detail {
  padding: 1rem 3rem 0;
}

@media (max-width: 768px) {
  .about__content {
    width: 90%;
    padding: 2rem;
  }

  .about__skills {
    margin: 1rem 1.5rem;
  }

  .about__detail {
    padding: .5rem 1.5rem 0;
  }
}

.home {
  position: relative;
}

.home__background {
  width: 100%;
  height: 100%;
}

.home__header {
  width: 17rem;
  position: absolute;
  top: 5rem;
  left: 2rem;
}

.home__header h1, .home__header h2 {
  color: #fff;
}

.home__arrow {
  position: absolute;
  bottom: 3rem;
  left: 50%;
  transform: translateX(-50%);
}

.home__arrow img {
  height: 3rem;
  width: 3rem;
}

@media (max-width: 768px) {
  .home__header {
    width: auto;
    top: 2rem;
    left: 1rem;
  }

  .home__arrow {
    bottom: 1.5rem;
  }
}

